.galaxy-card {
    display:flex;
    flex-direction: column;
    background-color: #C4C4C4;
    align-items: center;
    padding: 1vh 1vw;
    margin: 1vh 1vw;
    border: #2E8BAF solid 3px;
    flex-basis: 25vw;
    max-height: 20vh;
    max-width: 300px;
    border-radius: 5%;
    box-shadow:
    inset 0 0 0 2px hsl(0, 0%, 80%),
    0 0 0 3px hsl(0, 0%, 90%);
        
}

.galaxy-name {
    flex-basis: 20%;
    font-weight: bold;
    color: #2E8BAF;
    text-align: center;
    margin: 0 0 2% 0;
    place-content: center;
    place-items: center;
}

.galaxy-image {
    flex-basis:10%;
    width: 60%;
    height: auto;
    place-content: center;
    place-items: center;
    padding: 1% 0;
    margin: 0 0 2% 0;
}

